<template>
  <div class="videoplay">
    <video  :src="url" controls v-if="url" class="videoplay__item">
    </video>
  </div>
</template>

<script>
import { getVideoUrl } from '../api/api_video'
export default {
  name: 'VideoPlay',
  data () {
    return {
      vid: this.$route.params.vid,
      url: null
    }
  },
  methods: {
    async getData (id) {
      const res = await getVideoUrl(id)
      console.log('videourl', res)
      this.url = res.data.urls[0].url
    }
  },
  created () {
    console.log('vid', this.vid)
    this.getData(this.vid)
  }
}
</script>

<style lang="scss" scoped>
.videoplay{
  width: 100%;
  height: 100%;
  &__item{
    margin: 0 5%;
    width: 90%;
    aspect-ratio: 4/3;
    max-height: 90%;
  }
}
</style>
